import React, { useEffect, useState, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getOrderStatuses, fireSnake } from "../../store//actions";
import { useTranslation } from "react-i18next";
import SelectInput from "./SelectInput";

let timeout;
const OrderStatuses = ({
  initialValue,
  name,
  getResult,
  touched,
  serverError,
}) => {
  const [statuses, setStatuses] = useState([]);
  const dispatch = useDispatch();
  const orders = useSelector((state) => state.orders);
  const { t } = useTranslation();

  const fetchStatuses = useCallback(async () => {
    try {
      const stats = await dispatch(getOrderStatuses());
      let fetchedStats = [];
      if (stats.length) {
        fetchedStats = stats.map((s) => {
          return {
            value: s.order_status_id,
            text: s.text,
            code: s.code,
          };
        });
      }
      setStatuses(fetchedStats);
    } catch (err) {
      dispatch(fireSnake("warning", t("orders:fetch_status_err")));
    }
  }, [dispatch, t]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchStatuses();
    }, 0);
    return () => clearTimeout(timeout);
  }, [fetchStatuses]);

  const getStatusCode = (status_id) => {
    const status = statuses.find((s) => s.value === status_id);
    if (status) {
      return status.code;
    }
    return "unknown";
  };
  return (
    <SelectInput
      options={[{ value: " ", text: t("common:all") }, ...statuses]}
      name={name}
      label={t("common:status")}
      initialValue={initialValue || " "}
      getResult={(value, validity, name) =>
        getResult(value, validity, name, getStatusCode(value))
      }
      loading={orders.itemLoading}
      required
      setTouched={touched}
      serverError={serverError}
    />
  );
};

export default OrderStatuses;
