import React, {
  useState,
  useRef,
  useEffect,
  Fragment,
  useCallback,
  useContext,
} from "react";
import {
  Typography,
  Box,
  Grid,
  IconButton,
  Tooltip,
  Paper,
} from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../../store/actions";
import { useTranslation } from "react-i18next";
import MaterialTable from "material-table";
import IconTextNotice from "../../components/common/IconTextNotice";
import Ttext from "../../components/common/Ttext";
import { arSA } from "date-fns/locale";
import { tableIcons } from "../../util/tableIcons";
import Input from "../../components/Inputs/Input";
import { Link } from "react-router-dom";
import VisibilityRoundedIcon from "@material-ui/icons/VisibilityRounded";
import PageError from "../../components/common/PageError";
import { format } from "date-fns";
import i18n from "../../i18n";
import TableSkeleton from "../../components/common/TableSkeleton";
import ShoppingCartRoundedIcon from "@material-ui/icons/ShoppingCartRounded";
import themeContext from "../../theme/themeContext";
import SelectInput from "../../components/Inputs/SelectInput";
import OrderStatuses from "../../components/Inputs/OrderStatuses";

const tableHeaders = [
  {
    title: <Ttext ns="common" text="number" />,
    field: "invoice_no",
    // width: 1,
  },
  {
    title: <Ttext ns="orders" text="total" />,
    field: "total",
    // width: 1,
  },
  {
    title: <Ttext ns="orders" text="payment_method" />,
    field: "payment_method",
  },
  {
    title: <Ttext ns="orders" text="order_date" />,
    field: "date_added",
    render: (row) =>
      format(new Date(row.date_added), "yyyy MMMM dd hh:mm:ss aaaa", {
        locale: i18n.language === "ar" ? arSA : "",
      }),
  },
  {
    title: <Ttext ns="common" text="status" />,
    field: "status",
  },
  {
    title: <Ttext ns="orders" text="user" />,
    field: "user",
    render: (row) => (
      <div className="flex__centerlize">
        <Typography variant="body2">{row.user}</Typography>
        <Typography className="ltr" variant="body2">
          {row.mobile}
        </Typography>
      </div>
    ),
    // width: 1,
    // sorting: false,
  },
  {
    title: <Ttext ns="orders" text="tracking" />,
    field: "tracking",
    render: (row) => (
      <div className="flex__centerlize">
        <Typography>{row.tracking}</Typography>
        <Typography>{row.tracking_company}</Typography>
      </div>
    ),
    // width: 1,
    // sorting: false,
  },
];

const initialQuery = {
  q: "",
  page: 0,
  perPage: 10,
  sort: "date_added",
  direction: "asc",
  payment_method: "",
  order_status: "",
};
let timeout;

const Orders = ({ location }) => {
  const [query, setQuery] = useState(initialQuery);
  const orders = useSelector((state) => state.orders);
  const [mounted, setMounted] = useState(false);
  const { primary_light, primary_light_color } = useContext(themeContext);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const tableRef = useRef();
  const hasPagination = orders.orders.pagination;

  const handleQuery = (value, validity, name) => {
    setQuery((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const fetchOrders = useCallback(async () => {
    try {
      await dispatch(actions.getOrders(query));
    } finally {
      setMounted(true);
    }
  }, [dispatch, query]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchOrders();
    }, 500);
    return () => clearTimeout(timeout);
  }, [query, fetchOrders]);

  return (
    <Fragment>
      <Grid container spacing={2} alignItems="center" className="py">
        <Grid item xs={8}>
          <Typography variant="h4" className="py">
            {t("orders:orders_list")}
          </Typography>
        </Grid>
        <Grid item xs={4}>
          <Box className="flex align-items-center xp">
            <Box
              width={30}
              height={15}
              className={`box-shadow ${primary_light}`}
            />
            <Typography variant="caption" className="px">
              {t("orders:needs_attention")}
            </Typography>
          </Box>
        </Grid>
        <Grid item sm={6} xs={12}>
          <Input
            label={t("common:searchhhh")}
            getResult={handleQuery}
            name="q"
            initialValue={query.q}
          />
        </Grid>
        <Grid item sm={3} xs={6}>
          <SelectInput
            options={[
              { value: " ", text: t("common:all") },
              { value: "cod", text: t("orders:cod") },
              { value: "credit_card", text: t("orders:credit_card") },
            ]}
            name="payment_method"
            label={t("orders:payment_method")}
            initialValue={query.payment_method || " "}
            getResult={handleQuery}
          />
        </Grid>
        <Grid item sm={3} xs={6}>
          <OrderStatuses
            name="order_status"
            initialValue={query.order_status}
            getResult={handleQuery}
          />
        </Grid>
      </Grid>

      {!mounted ? (
        <Paper>
          <TableSkeleton row={2} />
        </Paper>
      ) : !orders.loading && orders.errors ? (
        <PageError
          text={orders.errors.message || t("common:uncommon_err")}
          onTryAgain={fetchOrders}
        />
      ) : (
        <MaterialTable
          icons={tableIcons}
          columns={tableHeaders}
          tableRef={tableRef}
          data={orders.orders.orders}
          components={{
            Toolbar: (props) => " ",
            Actions: (props) => {
              const order = props.data;
              return (
                <Tooltip title={t("common:view")}>
                  <Link to={`${location.pathname}/view/${order.order_id}`}>
                    <IconButton>
                      <VisibilityRoundedIcon color="primary" />
                    </IconButton>
                  </Link>
                </Tooltip>
              );
            },
          }}
          actions={[
            {
              icon: () => null,
              onClick: () => null,
              hidden: true,
            },
          ]}
          options={{
            detailPanelColumnAlignment: "right",
            actionsColumnIndex: -1,
            draggable: false,
            emptyRowsWhenPaging: false,
            search: false,
            pageSize: query.perPage,
            pageSizeOptions: [5, 10, 20, 50],
            thirdSortClick: false,
            showTitle: false,
            rowStyle: (row) => {
              return {
                background:
                  row.order_status_id === 3 ? primary_light_color : "",
              };
            },
          }}
          onChangeRowsPerPage={(size) => handleQuery(size, true, "perPage")}
          onOrderChange={(orderBy, direction) => {
            const sort = tableHeaders[orderBy].field;
            setQuery((prev) => {
              return {
                ...prev,
                sort: sort,
                direction: direction,
              };
            });
          }}
          totalCount={hasPagination ? hasPagination.totalCount : 0}
          page={hasPagination ? hasPagination.currentPage : 1}
          onChangePage={(page) => handleQuery(page, true, "page")}
          localization={{
            header: {
              actions: "",
            },
            body: {
              emptyDataSourceMessage: orders.loading
                ? t("common:loadingggg")
                : !orders.loading &&
                  !orders.errors && (
                    <IconTextNotice
                      Icon={ShoppingCartRoundedIcon}
                      text={t("orders:no_orders")}
                    />
                  ),
              dateTimePickerLocalization: i18n.language === "ar" ? arSA : "",
            },
            pagination: {
              labelDisplayedRows:
                "{from}-{to} " + t("common:from") + " {count}",
              labelRowsSelect: t("common:perpage"),
            },
          }}
          isLoading={orders.loading}
        />
      )}
    </Fragment>
  );
};

export default Orders;
