import React from "react";
import { Box, Typography, Button } from "@material-ui/core";
import { useTranslation } from "react-i18next";
import CachedIcon from "@material-ui/icons/Cached";

const IconTextNotice = ({ Icon, text, color, onTryAgain }) => {
  const { t } = useTranslation();
  return (
    <Box padding={5} className="center">
      <Icon
        color={color || "primary"}
        style={{ fontSize: 200, opacity: 0.1 }}
      />
      <Typography variant="body2">{text}</Typography>
      {onTryAgain && (
        <Box className="m">
          <Button
            onClick={onTryAgain}
            color="primary"
            variant="outlined"
            endIcon={<CachedIcon />}
          >
            {t("common:tryagain")}
          </Button>
        </Box>
      )}
    </Box>
  );
};

export default IconTextNotice;
